import React from "react"
import Layout from "../components/Layout"
import HeroSide from "../components/pageComponents/HeroSide"
import Hero from "../components/pageComponents/Hero"
import { graphql } from 'gatsby'
import Seo from 'gatsby-plugin-wpgraphql-seo';
import PostGrid from "../components/pageComponents/PostGrid"


export const pageQuery = graphql`
query PageById($id: String) {
  wpPage(id: {eq: $id}) {
    id
    title
    uri
    pageComponents {
      fieldGroupName
      pageComponents {
        __typename
        ...HeroComponentSide
      }
    }
    seo {
        title
        metaDesc
        focuskw
        metaKeywords
        metaRobotsNoindex
        metaRobotsNofollow
        opengraphTitle
        opengraphDescription
        opengraphImage {
            altText
            sourceUrl
            srcSet
            localFile {
                childImageSharp {
                    gatsbyImageData(layout: FIXED, placeholder: BLURRED, formats: [AUTO, WEBP, AVIF])
                }
            }
        }
        twitterTitle
        twitterDescription
        twitterImage {
            altText
            sourceUrl
            srcSet
            localFile {
                childImageSharp {
                    fixed {
                    src
                    }
                }
            }
        }
        canonical
        cornerstone
        schema {
            articleType
            pageType
            raw
        }
    }
  }
  ...PostItems
  ...NewsItems
  ...EventItems
}
`;


const Home = ({ data }) => {
    console.log(data)
  const pageComponents = data.wpPage.pageComponents?.pageComponents;
  
  return (
    <Layout>
      <Seo post={data.wpPage} />
      <div>
      {pageComponents?.map( (component, i) => {
        const typeName = component.__typename
        const key = data.wpPage.id + typeName + i
        switch (typeName) {
          case 'WpPage_Pagecomponents_PageComponents_HeroComponent':
            return (data.wpPage.uri === '/home-alternate/' ? <HeroSide key={key} {...component}/> : <Hero key={key} {...component}/>)

          default:
            return <div> No Component defined in gatsby </div>
        }
      })}
    </div>
    
    </Layout>
  )
}

export default Home